/*blocks*/
.header-contacts {
  display: flex;
  justify-content: space-between;
  height: 45px; }

.header-contacts__post,
.header-contacts__phone {
  position: relative;
  font-family: 'PT Mono', "Arial", sans-serif;
  font-style: normal;
  font-weight: normal;
  font-size: 16px;
  line-height: 18px;
  line-height: 45px;
  color: #fff;
  text-decoration: none;
  padding: 0 0 0 48px; }

.header-contacts__post {
  margin: 0 88px 0 0; }
  .header-contacts__post:before {
    content: "";
    position: absolute;
    left: 0;
    top: calc(50% - 7px);
    width: 19px;
    height: 14px;
    background-image: url("../../img/post.svg");
    background-repeat: no-repeat;
    background-size: cover; }

.header-contacts__phone {
  margin: 0 38px 0 0; }
  .header-contacts__phone:before {
    content: "";
    position: absolute;
    left: 0;
    top: calc(50% - 9px);
    width: 18px;
    height: 18px;
    background-image: url("../../img/phone.svg");
    background-repeat: no-repeat;
    background-size: cover; }

.header-contacts__call {
  width: 180px;
  height: 45px;
  font-family: 'Roboto Condensed', "Arial", sans-serif;
  font-weight: 700;
  font-size: 14px;
  line-height: 16px;
  text-align: center;
  letter-spacing: 0.05em;
  text-transform: uppercase;
  background-color: transparent;
  border: 1px solid #fff;
  box-sizing: border-box;
  border-radius: 5px;
  color: #fff; }
  .header-contacts__call:hover {
    cursor: pointer; }

.logo-text.logo-text--header {
  color: #fff; }

.logo-text.logo-text--first {
  font-family: 'Roboto Condensed', "Arial", sans-serif;
  font-style: normal;
  font-weight: 700;
  font-size: 25px;
  line-height: 29px;
  margin: 0; }

.logo-text.logo-text--second {
  font-family: 'Roboto', "Arial", sans-serif;
  font-style: normal;
  font-weight: 500;
  font-size: 12px;
  line-height: 14px;
  margin: 0;
  color: #788891; }

.page-header {
  display: flex;
  justify-content: space-between;
  align-items: center;
  width: 1411px;
  height: auto;
  margin: 37px auto 0 auto;
  z-index: 10; }

.page {
  display: grid;
  grid-template-rows: auto 1fr auto;
  min-height: 100%;
  width: 100%;
  margin: 0;
  padding: 0;
  color: #fff; }

.wrapper-logo {
  display: flex;
  justify-content: space-between;
  height: 52px;
  width: 202px; }

.wrapper-logo__text {
  width: 100%;
  height: 100%;
  margin: 6px 0 0 16px; }

.wrapper-main-info {
  width: 100%;
  height: 967px;
  padding: 89px 0 0 0;
  margin: -89px 0 0 0;
  background-color: #1B2D37;
  background-image: url("../../img/main__desk.png");
  background-repeat: no-repeat;
  background-size: cover; }
